import "./Soilform.css";
import { useState } from "react";

function Soilform({ setResultSummary }) {
  return (
    <>
      <div className="container1 shadow-md bg-gradient-to-r from-blue-200 to-green-500 hover:from-green-300 hover:to-blue-300">
        <h3 className="bg-gradient-to-r from-green-400 to-green-500 hover:to-green-400 font-mono text-3xl text-blue-500 hover:text-green-700">
          Click to view each contents:
        </h3>

        <nav className="bg-gradient-to-r from-green-400 to-green-500 hover:to-green-400 font-mono text-3xl text-blue-500 hover:text-green-700">
          <ul>
            <li>
              <a className="text-blue-300 text-green-800" href="#what-is">
                What is Soil Erosion?
              </a>
            </li>
            <li>
              <a className="text-blue-300 text-green-800" href="#causes">
                Causes of Soil Erosion
              </a>
            </li>
            <li>
              <a className="text-blue-300 text-green-800" href="#effects">
                Effects of Soil Erosion
              </a>
            </li>
            <li>
              <a className="text-blue-300 text-green-800" href="#prevention">
                Prevention of Soil Erosion
              </a>
            </li>
            <li>
              <a className="text-blue-300 text-green-800" href="#sustainable">
                Sustainable practices
              </a>
            </li>
            <li>
              <a className="text-blue-300 text-green-800" href="#IT-relation">
                Relationship between IT sector and soil erosion prevention
              </a>
            </li>
          </ul>
        </nav>
        <section id="what-is" className="text-red-900">
          <center>
            <h2 className="p-10 text-3xl font-mono hover:text-blue-800 text-green-800">
              What is Soil Erosion?
            </h2>
          </center>

          <ul>
            <p className="p-2 text-xl font-mono hover:text-blue-800 text-green-800">
              Soil erosion is the natural process by which soil is moved from
              one location to another. It occurs when the soil is washed away or
              blown away by the forces of wind, water, or gravity. Soil erosion
              is a serious environmental problem, as it can result in the loss
              of fertile topsoil, decreased soil productivity, and damage to
              natural habitats. There are several factors that contribute to
              soil erosion, including the type of soil, the slope of the land,
              the intensity of rainfall or wind, the presence of vegetation or
              other natural barriers, and human activities such as
              deforestation, overgrazing, and farming practices that involve
              tilling the soil or leaving it bare. Soil erosion can have a range
              of negative impacts on the environment and human society. For
              example, it can lead to the loss of crop yields, reduced water
              quality, increased flooding, and soil degradation. It can also
              contribute to climate change by releasing carbon stored in the
              soil into the atmosphere. There are a variety of techniques that
              can be used to prevent or reduce soil erosion, including planting
              vegetation, building terraces or retaining walls, implementing
              conservation tillage practices, and using cover crops or other
              soil amendments to improve soil health. In some cases, engineering
              solutions such as the construction of check dams or other erosion
              control structures may also be used. Overall, soil erosion is a
              complex issue that requires careful management and intervention in
              order to protect soil health and maintain the health of natural
              ecosystems.
            </p>
          </ul>
        </section>
        <section id="causes">
          <center>
            <h2 className="p-10 text-3xl font-mono hover:text-blue-800 text-green-800">
              Causes of Soil Erosion:{" "}
            </h2>
          </center>
          <p className="p-2 text-xl font-mono hover:text-blue-800 text-green-800">
            <ul>
              Soil erosion is the process of the removal of topsoil by natural
              or human-induced factors, including wind, water, and tillage. Here
              are some of the main causes of soil erosion:
              <li>
                Water erosion: This type of erosion occurs when rainwater or
                snowmelt carries away topsoil. The force of the water can create
                gullies and ravines in the soil.
              </li>
              <li>
                Wind erosion: Wind erosion is caused by the movement of wind
                over exposed soil. This can occur in areas with little
                vegetation, such as deserts or agricultural fields with tilled
                soil.
              </li>
              <li>
                Agricultural practices: Plowing, tilling, and harvesting can
                cause soil erosion by exposing the soil to wind and water
                erosion. Overgrazing by livestock can also cause soil erosion by
                removing vegetation and compacting the soil.
              </li>
              <li>
                Deforestation: Trees help to anchor soil in place and absorb
                water. When forests are cleared, the soil becomes more
                vulnerable to erosion.
              </li>
              <li>
                Urbanization: The development of urban areas can increase the
                amount of impervious surfaces, such as roads and buildings. This
                can lead to increased runoff and erosion.
              </li>
              <li>
                Mining activities: Mining can cause soil erosion by removing
                vegetation and exposing the soil to wind and water erosion.
                Mining can also create new channels for water to flow, leading
                to increased erosion.
              </li>
              <li>
                Climate change: Changes in climate can increase the frequency
                and intensity of storms, leading to more water erosion. Droughts
                can also increase wind erosion.
              </li>
              <p>
                Overall, soil erosion is a significant environmental problem
                that can lead to reduced agricultural productivity, increased
                sedimentation in waterways, and decreased biodiversity.
              </p>
            </ul>
          </p>
        </section>
        <center>
          <h2 className="p-10 text-3xl font-mono hover:text-blue-800 text-green-800">
            Effects of Soil Erosion:{" "}
          </h2>
        </center>
        <ul>
          <p className="p-2 text-xl font-mono hover:text-blue-800 text-green-800">
            Soil erosion is a significant environmental problem that can have
            far-reaching effects on ecosystems and human communities. Here are
            some of the main effects of soil erosion:
            <li>
              Reduced agricultural productivity: Soil erosion can remove the
              nutrient-rich topsoil that is necessary for plant growth, leading
              to reduced crop yields and lower agricultural productivity.
            </li>
            <li>
              Increased sedimentation in waterways: Soil that is eroded from
              fields can be carried into nearby streams, rivers, and lakes,
              leading to increased sedimentation. This can reduce water quality
              and harm aquatic ecosystems.
            </li>
            <li>
              Increased risk of flooding: Soil erosion can change the landscape
              and create new channels for water to flow, leading to increased
              risk of flooding.
            </li>
            <li>
              Loss of biodiversity: Soil erosion can remove habitat and food
              sources for plants and animals, leading to a loss of biodiversity.
            </li>
            <li>
              Increased greenhouse gas emissions: Soil that is eroded from
              fields can release carbon into the atmosphere, leading to
              increased greenhouse gas emissions.
            </li>
            <li>
              Decreased carbon storage: Soil erosion can remove the
              nutrient-rich topsoil that is necessary for carbon storage,
              leading to decreased carbon storage in the soil.
            </li>
            <li>
              Soil compaction: Soil erosion can lead to soil compaction, which
              can reduce water infiltration and nutrient uptake by plants.
            </li>
            <li>
              Overall, soil erosion is a serious environmental problem that can
              have significant economic and ecological consequences. Mitigating
              soil erosion requires sustainable land management practices, such
              as conservation tillage, cover cropping, and reduced tillage.
            </li>
          </p>
        </ul>

        <section id="prevention">
          <center>
            <h2 className="p-10 text-3xl font-mono hover:text-blue-800 text-green-800">
              Prevention of Soil Erosion:{" "}
            </h2>
          </center>
          <p className="p-2 text-xl font-mono hover:text-blue-800 text-green-800">
            <ul>
              Soil erosion is a significant problem that can lead to the loss of
              valuable topsoil, reduce soil fertility, and degrade the quality
              of the environment. Here are some ways to prevent soil erosion:
              <li>
                Plant cover crops: Planting cover crops such as clover, rye, and
                alfalfa can help to protect the soil from erosion. These crops
                protect the soil from wind and water erosion by holding the soil
                together with their roots.
              </li>
              <li>
                Reduce tillage: Over-tilling can cause soil erosion by exposing
                the soil to wind and water. By reducing the frequency of tillage
                or switching to no-till farming, the soil structure remains
                intact and helps to reduce soil erosion.
              </li>
              <li>
                Terracing: Terracing is a method of shaping the land into steps,
                which helps to slow down water runoff and reduce soil erosion.
                Terraces can be made on hillsides to prevent soil from washing
                down the slopes.
              </li>
              <li>
                Contour farming: Contour farming is the practice of planting
                crops across the slope of the land rather than up and down. This
                helps to reduce water runoff and slows down the speed of water
                flowing down the slope.
              </li>
              <li>
                Mulching: Mulching is the practice of covering the soil with
                organic material, such as straw or leaves, which helps to
                prevent soil erosion by reducing the impact of raindrops on the
                soil surface.
              </li>
              <li>
                Windbreaks: Planting windbreaks, such as trees and shrubs, can
                help to protect the soil from wind erosion by reducing the force
                of the wind on the soil surface.
              </li>
              <li>
                By adopting these practices, we can prevent soil erosion and
                help to maintain soil health, fertility, and productivity.
              </li>
            </ul>
          </p>
        </section>

        <section id="sustainable">
          <center>
            {" "}
            <h2 className="p-10 text-3xl font-mono hover:text-blue-800 text-green-800">
              Sustainable practices for soil erosion :{" "}
            </h2>
          </center>
          <ul>
            <p className="p-2 text-xl font-mono hover:text-blue-800 text-green-800">
              Sustainable land management practices can help to prevent soil
              erosion and promote the long-term health of soil and ecosystems.
              Awareness and education about sustainable practices are crucial
              for encouraging their adoption and implementation. Here are some
              ways to promote awareness and education for sustainable land
              management:
              <li>
                Provide training and education programs: Farmers, landowners,
                and other stakeholders should be provided with training and
                education programs on sustainable land management practices,
                including soil conservation, agroforestry, and cover cropping.
              </li>
              <li>
                Use extension services: Extension services can provide
                on-the-ground support to farmers and landowners, helping them to
                implement sustainable practices and providing information on the
                latest research and innovations.
              </li>
              <li>
                Develop partnerships: Collaborative partnerships between
                government agencies, non-governmental organizations, and private
                sector stakeholders can help to promote sustainable land
                management practices and encourage the adoption of best
                practices.
              </li>
              <li>
                Raise public awareness: Public awareness campaigns can help to
                increase understanding of the importance of soil conservation
                and sustainable land management practices, and encourage
                consumers to support environmentally-friendly practices.
              </li>
              <li>
                Encourage sustainable policy: Governments can promote
                sustainable land management practices by developing policies and
                regulations that support conservation and sustainable use of
                natural resources.
              </li>
              <li>
                By promoting awareness and education for sustainable land
                management practices, we can help to prevent soil erosion,
                protect ecosystems, and ensure the long-term health and
                productivity of our soils.
              </li>
            </p>
          </ul>
        </section>
        <section id="IT-relation">
          <center>
            <h2 className="p-10 text-3xl font-mono hover:text-blue-800 text-green-800">
              {" "}
              Relation between IT sector and soil erosion prevention:{" "}
            </h2>
          </center>
          <ul>
            <p
              p
              className="p-2 text-xl font-mono hover:text-blue-800 text-green-800"
            >
              At first glance, it may seem that there is little relation between
              the IT sector and soil erosion prevention. However, technology can
              play a significant role in promoting sustainable land management
              practices and preventing soil erosion. Here are some examples:
              <li>
                GIS mapping: Geographic information systems (GIS) can be used to
                map soil types, topography, and land use patterns. This
                information can help farmers and landowners to make informed
                decisions about crop selection, land use, and soil conservation
                practices.
              </li>
              <li>
                Precision agriculture: Technologies such as sensors, drones, and
                GPS can help farmers to apply inputs such as fertilizers and
                pesticides more precisely, reducing waste and minimizing the
                environmental impact.
              </li>
              <li>
                Digital soil mapping: Digital soil mapping can help to identify
                areas at risk of soil erosion and prioritize soil conservation
                efforts.
              </li>
              <li>
                Climate-smart agriculture: Information and communication
                technologies (ICTs) can be used to provide weather and climate
                information, enabling farmers to make decisions about planting,
                harvesting, and other agricultural activities that are more
                climate-resilient.
              </li>
              <li>
                E-commerce: E-commerce platforms can help to promote the
                adoption of sustainable agricultural practices by providing
                access to inputs such as organic fertilizers and cover crop
                seeds.
              </li>
              <li>
                By leveraging technology to promote sustainable land management
                practices, the IT sector can play a critical role in preventing
                soil erosion and promoting the long-term health of our soils and
                ecosystems.
              </li>
            </p>
          </ul>
        </section>
        <center>
          <p p className="p-2 font-mono hover:text-blue-800 text-green-800">
            This project website is basically aimed at providing awareness on
            sustainable practice awareness for soil erosion.{" "}
          </p>
        </center>
        <center>
          <h1 className="p-10 text-3xl font-mono hover:text-blue-800 text-green-800">
            THANK YOU
          </h1>
        </center>
        <footer>
          <p>&copy; 2023 Soil Erosion Website</p>
        </footer>
      </div>
    </>
  );
}

export default Soilform;
